@import "../styles/general.scss";

@font-face {
  font-family: "Montserrat", sans-serif;
  src: url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap");
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
  box-sizing: border-box;
}

html {
  margin: 0;
  padding: 0;
  min-height: 80vh;
  width: 100%;
  box-sizing: border-box;
}
body {
  display: flex;
  position: relative;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  overflow-x: hidden;
  box-sizing: border-box;

  
}
