@use "./gtl";
@import "./gtl";

.View {
  display: grid;
  justify-self: end;
  align-self: baseline;

  &__button {
    @include button($type: "change-view");
    margin: gtl.$margin;
    width: calc(gtl.$margin-default * 2.1);
    height: calc(gtl.$margin-default * 2.1);
    padding: 0;
    box-sizing: border-box;
    padding: 2px;
    img {
      -webkit-filter: invert(100%); /* Safari 6.0 - 9.0 */
      filter: invert(100%);
      margin: auto;
    }
    img[alt=reorder_img] {
      padding: 3px;
      height: 100%;
      width: auto;
    }
    img[alt=largeIcon_img]{
      padding: 1px;
      height: 100%;
      width: auto
    }
  }
}

